import { Node, DOMSerializer } from "prosemirror-model";
import { EditorState, Plugin, PluginKey } from "prosemirror-state";
import { EditorView } from "prosemirror-view";
import { createSchema } from "./schema-basic";
import { exampleSetup } from "./prosemirror-example-setup";
import { RichValue, PackageResourceUrl, Config, ImageType, UploadFiles } from "./models";

const createState = function (config: Config, json?: RichValue): EditorState {
    const schema = createSchema(config.packageResourceUrl);
    return EditorState.create({
        schema,
        plugins: exampleSetup({ schema: schema, config: config }),
        doc: json ? Node.fromJSON(schema, json) : undefined
    });
};

export function getDefaultRichText(): RichValue {
    return {
        type: "doc",
        content: [
            {
                type: "paragraph",
                content: []
            }
        ]
    };
}

export function isEmptyRichText(json: RichValue): boolean {
    if (!json || "doc" != json.type || !json.content || !json.content.length) {
        return true;
    }
    if (1 < json.content.length) {
        return false;
    }
    let firstRow = json.content[0];
    if (!firstRow || !firstRow.content || !firstRow.content.length) {
        return true;
    }
    if (1 < firstRow.content.length) {
        return false;
    }
    let firstCol = firstRow.content[0];
    if ("text" == firstCol.type) {
        return !firstCol.text;
    } else {
        return false;
    }
}

