type UploadCallback = (resourceKey: string | number) => void;

function collectFileTasks(data: RichValue, fileTasks: Map<File, UploadCallback[]>) {
    const type = typeof data;
    if (data instanceof Array) {
        for (let child of data) {
            collectFileTasks(child, fileTasks);
        }
    } else if (type === "object" && data) {
        if (data.type) {
            let attrs = data.attrs;
            let content = data.content;
            if (attrs && ImageType.File == attrs.imageType) {
                let file = attrs.file;
                let callbacks = fileTasks.get(file);
                if (!callbacks) {
                    callbacks = [];
                    fileTasks.set(file, callbacks);
                }
                callbacks.push((resourceKey) => {
                    attrs.imageType = ImageType.Resource;
                    attrs.resourceKey = resourceKey;
                    delete attrs.file;
                    delete attrs.src;
                });
            } else {
                collectFileTasks(content, fileTasks);
            }
        }
    }
}

export async function uploadResource(uploadFiles: UploadFiles, json: RichValue) {
    let fileTasks: Map<File, UploadCallback[]> = new Map();
    collectFileTasks(json, fileTasks);
    if (fileTasks.size) {
        let files = Array.from(fileTasks.keys());
        let resourceKeys = await uploadFiles(files);
        for (let i = 0; i < files.length; i++) {
            let resourceKey = resourceKeys[i];
            let file = files[i];
            let callbacks = fileTasks.get(file);
            if (callbacks) {
                for (let callback of callbacks) {
                    callback(resourceKey);
                }
            }
        }
    }
}

export function renderRichText(json: RichValue, packageResourceUrl: PackageResourceUrl): DocumentFragment {
    const schema = createSchema(packageResourceUrl);
    return DOMSerializer.fromSchema(schema).serializeFragment(Node.fromJSON(schema, json).content) as any;
}

export function mountRichText(el: HTMLElement, config: Config, json?: RichValue): EditorView {
    let view = new EditorView(el, {
        state: createState(config, json),
        dispatchTransaction(transaction) {
            let newState = view.state.apply(transaction);
            view.updateState(newState);
            if (config.change) {
                config.change(newState.doc.toJSON());
            }
        },
        plugins: [new Plugin({
            key: new PluginKey('focus'),
            props: {
                handleDOMEvents: {
                    focus: (view) => {
                        if (config.focus) {
                            config.focus(view);
                        }
                        return true;
                    },
                    blur: (view) => {
                        if (config.blur) {
                            config.blur(view);
                        }
                        return true;
                    }
                }
            }
        })]
    });
    return view;
}

export function updateRichText(view: EditorView, config: Config, json: RichValue) {
    view.updateState(createState(config, json));
}

export function unmountRichText(view: EditorView) {
    view.destroy();
}
