import { EditorView } from "prosemirror-view";

export type RichValue = { [key: string]: any }

export enum ImageType {
    Url = "Url",
    File = "File",
    Resource = "Resource",
}

export interface Image {
    imageType: ImageType;
    url?: string;
    file?: File;
    resourceKey?: string | number;
}

export type UploadFiles = (files: File[]) => Promise<(string | number)[]>;
export type PackageResourceUrl = (key: string | number) => string;

export interface Config {
    getLink: () => Promise<{
        href: string;
        title: string;
    } | void>;
    pickColor: () => Promise<string | void>;
    getResource: () => Promise<Image | void>;
    packageResourceUrl: PackageResourceUrl;
    placeholder?: string;
    change?: (json: RichValue) => void;
    blur?: (view: EditorView) => void;
    focus?: (view: EditorView) => void;
}
