import type { Schema, NodeType, MarkType, Mark } from 'prosemirror-model';
import {
    wrapItem, blockTypeItem, Dropdown, DropdownSubmenu, joinUpItem, liftItem,
    undoItem, redoItem, icons, MenuItem, MenuElement, MenuItemSpec
} from "prosemirror-menu"
import { EditorView } from 'prosemirror-view';
import { EditorState, NodeSelection, Transaction } from "prosemirror-state"
import { toggleMark } from "prosemirror-commands"
import { wrapInList } from "prosemirror-schema-list"
import { Config, ImageType } from "../models"
import { BLOB_CACHE } from "../schema-basic"

// Helpers to create specific types of items

function canInsert<S extends Schema = any>(state: EditorState, nodeType: NodeType) {
    let $from = state.selection.$from
    for (let d = $from.depth; d >= 0; d--) {
        let index = $from.index(d)
        if ($from.node(d).canReplaceWith(index, index, nodeType)) return true
    }
    return false
}

function insertImageItem<S extends Schema = any>(nodeType: NodeType, config: Config) {
    return new MenuItem({
        title: "Insert image",
        label: "Image",
        enable(state) { return canInsert(state, nodeType) },
        async run(state, _, view) {
            let image = await Promise.resolve(config.getResource());
            if (null != image) {
                let src: string | undefined;
                let resourceKey: string | number | undefined;
                let file: File | undefined;
                if (ImageType.File == image.imageType) {
                    src = URL.createObjectURL(image.file!);
                    file = image.file;
                    BLOB_CACHE[src] = file!;
                } else if (ImageType.Resource == image.imageType) {
                    src = await Promise.resolve(config.packageResourceUrl(image.resourceKey!));
                    resourceKey = image.resourceKey;
                } else if (ImageType.Url == image.imageType) {
                    src = image.url!;
                }
                if (undefined != src) {
                    view.dispatch(view.state.tr.replaceSelectionWith(nodeType.createAndFill({
                        imageType: image.imageType,
                        src: src,
                        resourceKey: resourceKey,
                        file: file,
                    })!));
                    view.focus();
                }
            }
        }
    })
}

function cmdItem<S extends Schema = any>(cmd: (p1: EditorState, p2: (p: Transaction) => void, p3: EditorView, p4: Event) => void, options: Partial<MenuItemSpec>): MenuItem {
    return new MenuItem({
        run: cmd,
        ...options
    })
}

function markActive<S extends Schema = any>(state: EditorState, type: Mark | MarkType): boolean {
    let { from, $from, to, empty } = state.selection
    if (empty) return !!type.isInSet(state.storedMarks || $from.marks())
    else return state.doc.rangeHasMark(from, to, type)
}

function markItem<S extends Schema = any>(markType: MarkType, options: Partial<MenuItemSpec>): MenuItem {
    return cmdItem(toggleMark(markType), {
        active(state: EditorState) { return markActive(state, markType) },
        enable: (state) => true,
        ...options
    })
}

function linkItem<S extends Schema = any>(markType: MarkType, config: Config) {
    return new MenuItem({
        title: "Add or remove link",
        icon: icons.link,
        active(state) { return markActive(state, markType) },
        enable(state) { return !state.selection.empty },
        async run(state, dispatch, view) {
            if (markActive(state, markType)) {
                toggleMark(markType)(state, dispatch)
                return true
            }
            let link = await Promise.resolve(config.getLink());
            if (null != link) {
                toggleMark(markType, link)(view.state, view.dispatch)
                view.focus()
            }
        }
    })
}

function wrapListItem<S extends Schema = any>(nodeType: NodeType, options: {
    attrs?: { [key: string]: any };
    title: string;
    icon: any;
}) {
    return cmdItem(wrapInList(nodeType, options.attrs), options)
}

// :: (Schema) → Object
// Given a schema, look for default mark and node types in it and
// return an object with relevant menu items relating to those marks:
//
// **`toggleStrong`**`: MenuItem`
//   : A menu item to toggle the [strong mark](#schema-basic.StrongMark).
//
// **`toggleEm`**`: MenuItem`
//   : A menu item to toggle the [emphasis mark](#schema-basic.EmMark).
//
// **`toggleCode`**`: MenuItem`
//   : A menu item to toggle the [code font mark](#schema-basic.CodeMark).
//
// **`toggleLink`**`: MenuItem`
//   : A menu item to toggle the [link mark](#schema-basic.LinkMark).
//
// **`insertImage`**`: MenuItem`
//   : A menu item to insert an [image](#schema-basic.Image).
//
// **`wrapBulletList`**`: MenuItem`
//   : A menu item to wrap the selection in a [bullet list](#schema-list.BulletList).
//
// **`wrapOrderedList`**`: MenuItem`
//   : A menu item to wrap the selection in an [ordered list](#schema-list.OrderedList).
//
// **`wrapBlockQuote`**`: MenuItem`
//   : A menu item to wrap the selection in a [block quote](#schema-basic.BlockQuote).
//
// **`makeParagraph`**`: MenuItem`
//   : A menu item to set the current textblock to be a normal
//     [paragraph](#schema-basic.Paragraph).
//
// **`makeCodeBlock`**`: MenuItem`
//   : A menu item to set the current textblock to be a
//     [code block](#schema-basic.CodeBlock).
//
// **`makeHead[N]`**`: MenuItem`
//   : Where _N_ is 1 to 6. Menu items to set the current textblock to
//     be a [heading](#schema-basic.Heading) of level _N_.
//
// **`insertHorizontalRule`**`: MenuItem`
//   : A menu item to insert a horizontal rule.
//
// The return value also contains some prefabricated menu elements and
// menus, that you can use instead of composing your own menu from
// scratch:
//
// **`insertMenu`**`: Dropdown`
//   : A dropdown containing the `insertImage` and
//     `insertHorizontalRule` items.
//
// **`typeMenu`**`: Dropdown`
//   : A dropdown containing the items for making the current
//     textblock a paragraph, code block, or heading.
//
// **`fullMenu`**`: [[MenuElement]]`
//   : An array of arrays of menu elements for use as the full menu
//     for, for example the [menu bar](https://github.com/prosemirror/prosemirror-menu#user-content-menubar).


function cut<T>(arr: T[]): T[] {
    return arr.filter((x: T) => x)
}

function createSvg(data: string): Node {
    let container = document.createElement('div');
    container.innerHTML = data;
    return container.firstChild!;
}

export function buildMenuItems<S extends Schema = any>(schema: S, config: Config): MenuElement[][] {
    let r: {
        [key: string]: MenuElement;
    } = {}, type
    if (type = schema.marks.strong)
        r.toggleStrong = markItem(type, { title: "Toggle strong style", icon: icons.strong })
    if (type = schema.marks.em)
        r.toggleEm = markItem(type, { title: "Toggle emphasis", icon: icons.em })
    if (type = schema.marks.color) {
        r.setColor = cmdItem(async (state, dispatch, view) => {
            let markType = schema.marks.color;
            if (markActive(state, markType)) {
                toggleMark(markType)(state, dispatch);
                return;
            }
            let color = await Promise.resolve(config.pickColor());
            if (null != color) {
                toggleMark(markType, {
                    value: color
                })(state, dispatch);
                view.focus();
            }
        }, {
            title: "Color",
            icon: {
                dom: createSvg(`<svg style="transform: scale(1.3);" viewBox="0 0 24 24"><g fill-rule="evenodd"><path id="tox-icon-text-color__color" d="M3 18h18v3H3z"></path><path d="M8.7 16h-.8a.5.5 0 0 1-.5-.6l2.7-9c.1-.3.3-.4.5-.4h2.8c.2 0 .4.1.5.4l2.7 9a.5.5 0 0 1-.5.6h-.8a.5.5 0 0 1-.4-.4l-.7-2.2c0-.3-.3-.4-.5-.4h-3.4c-.2 0-.4.1-.5.4l-.7 2.2c0 .3-.2.4-.4.4zm2.6-7.6l-.6 2a.5.5 0 0 0 .5.6h1.6a.5.5 0 0 0 .5-.6l-.6-2c0-.3-.3-.4-.5-.4h-.4c-.2 0-.4.1-.5.4z"></path></g></svg>`),
            }
        });
    }
    if (type = schema.marks.bgColor) {
        r.setBgColor = cmdItem(async (state, dispatch, view) => {
            let markType = schema.marks.bgColor;
            if (markActive(state, markType)) {
                toggleMark(markType)(state, dispatch);
                return;
            }
            let bgColor = await Promise.resolve(config.pickColor());
            if (null != bgColor) {
                toggleMark(markType, {
                    value: bgColor
                })(state, dispatch);
                view.focus();
            }
        }, {
            title: "Background color",
            icon: {
                dom: createSvg(`<svg style="transform: scale(1.3);" viewBox="0 0 24 24"><g fill-rule="evenodd"><path id="tox-icon-highlight-bg-color__color" d="M3 18h18v3H3z"></path><path fill-rule="nonzero" d="M7.7 16.7H3l3.3-3.3-.7-.8L10.2 8l4 4.1-4 4.2c-.2.2-.6.2-.8 0l-.6-.7-1.1 1.1zm5-7.5L11 7.4l3-2.9a2 2 0 0 1 2.6 0L18 6c.7.7.7 2 0 2.7l-2.9 2.9-1.8-1.8-.5-.6"></path></g></svg>`),
            }
        });
    }
    if (type = schema.marks.code)
        r.toggleCode = markItem(type, { title: "Toggle code font", icon: icons.code })
    if (type = schema.marks.link)
        r.toggleLink = linkItem(type, config)

    if (type = schema.nodes.image)
        r.insertImage = insertImageItem(type, config)
    if (type = schema.nodes.bullet_list)
        r.wrapBulletList = wrapListItem(type, {
            title: "Wrap in bullet list",
            icon: icons.bulletList
        })
    if (type = schema.nodes.ordered_list)
        r.wrapOrderedList = wrapListItem(type, {
            title: "Wrap in ordered list",
            icon: icons.orderedList
        })
    if (type = schema.nodes.blockquote)
        r.wrapBlockQuote = wrapItem(type, {
            title: "Wrap in block quote",
            icon: icons.blockquote
        })
    if (type = schema.nodes.paragraph)
        r.makeParagraph = blockTypeItem(type, {
            title: "Change to paragraph",
            label: "Plain"
        })
    if (type = schema.nodes.code_block)
        r.makeCodeBlock = blockTypeItem(type, {
            title: "Change to code block",
            label: "Code"
        })
    if (type = schema.nodes.heading)
        for (let i = 1; i <= 10; i++)
            r["makeHead" + i] = blockTypeItem(type, {
                title: "Change to heading " + i,
                label: "Level " + i,
                attrs: { level: i }
            })
    if (type = schema.nodes.horizontalRule) {
        let hr = type
        r.insertHorizontalRule = new MenuItem({
            title: "Insert horizontal rule",
            label: "Horizontal rule",
            enable(state) { return canInsert(state, hr) },
            run(state, dispatch) { dispatch(state.tr.replaceSelectionWith(hr.create())) }
        })
    }
    r.insertMenu = new Dropdown(cut([r.insertImage, r.insertHorizontalRule]), { label: "Insert" })
    r.typeMenu = new Dropdown(cut([r.makeParagraph, r.makeCodeBlock, r.makeHead1 && new DropdownSubmenu(cut([
        r.makeHead1, r.makeHead2, r.makeHead3, r.makeHead4, r.makeHead5, r.makeHead6
    ]), { label: "Heading" })]), { label: "Type..." })

    let inlineMenu = [cut([r.toggleStrong, r.toggleEm, r.setColor, r.setBgColor, r.toggleCode, r.toggleLink])]
    let blockMenu = [cut([r.wrapBulletList, r.wrapOrderedList, r.wrapBlockQuote, joinUpItem,
        liftItem])]
    return inlineMenu.concat([[r.insertMenu, r.typeMenu]], [[undoItem, redoItem]], blockMenu);
}
