import { MarkSpec, NodeSpec, Schema, DOMOutputSpec } from 'prosemirror-model';
import { PackageResourceUrl, ImageType } from "./models";

/**
 * [Specs](#model.NodeSpec) for the nodes defined in this schema.
 */
interface Nodes {
    doc: NodeSpec;
    paragraph: NodeSpec;
    blockquote: NodeSpec;
    horizontalRule: NodeSpec;
    heading: NodeSpec;
    text: NodeSpec;
    image: NodeSpec;
    hardBreak: NodeSpec;
}

/**
* [Specs](#model.MarkSpec) for the marks in the schema.
*/
interface Marks {
    link: MarkSpec;
    em: MarkSpec;
    strong: MarkSpec;
    color: MarkSpec;
    bgColor: MarkSpec;
}

const pDOM: DOMOutputSpec = ["p", 0], blockquoteDOM: DOMOutputSpec = ["blockquote", 0], hrDOM: DOMOutputSpec = ["hr"],
    brDOM: DOMOutputSpec = ["br"]

export const BLOB_CACHE: {
    [url: string]: File;
} = {};

const emDOM: DOMOutputSpec = ["em", 0], strongDOM: DOMOutputSpec = ["strong", 0]

// :: Object [Specs](#model.MarkSpec) for the marks in the schema.
const marks: Marks = {
    // :: MarkSpec A link. Has `href` and `title` attributes. `title`
    // defaults to the empty string. Rendered and parsed as an `<a>`
    // element.
    link: {
        attrs: {
            href: {},
            title: { default: null }
        },
        inclusive: false,
        parseDOM: [{
            tag: "a[href]", getAttrs(dom: HTMLElement) {
                return { href: dom.getAttribute("href"), title: dom.getAttribute("title") }
            }
        }],
        toDOM(node) { let { href, title } = node.attrs; return ["a", { href, title }, 0] }
    },

    // :: MarkSpec An emphasis mark. Rendered as an `<em>` element.
    // Has parse rules that also match `<i>` and `font-style: italic`.
    em: {
        parseDOM: [{ tag: "i" }, { tag: "em" }, { style: "font-style=italic" }],
        toDOM() { return emDOM }
    },

    // :: MarkSpec A strong mark. Rendered as `<strong>`, parse rules
    // also match `<b>` and `font-weight: bold`.
    strong: {
        parseDOM: [{ tag: "strong" },
        // This works around a Google Docs misbehavior where
        // pasted content will be inexplicably wrapped in `<b>`
        // tags with a font-weight normal.
        { tag: "b", getAttrs: (node: HTMLElement) => node.style.fontWeight != "normal" && null },
        { style: "font-weight", getAttrs: (value: string) => /^(bold(er)?|[5-9]\d{2,})$/.test(value) && null }],
        toDOM() { return strongDOM }
    },
    color: {
        attrs: {
            value: {
                default: null
            },
        },
        parseDOM: [
            {
                style: "color",
                getAttrs: (value: string) => {
                    if (value && "inherit" != value && "transparent" != value) {
                        return {
                            value: value
                        };
                    } else {
                        return false;
                    }
                }
            }
        ],
        toDOM(mark) {
            let { value } = mark.attrs;
            return ["span", {
                style: `color: ${value};`
            }, 0]
        }
    },
    bgColor: {
        attrs: {
            value: {
                default: null
            },
        },
        parseDOM: [
            {
                style: "background-color",
                getAttrs: (value: string) => {
                    if (value && "inherit" != value && "transparent" != value) {
                        return {
                            value: value
                        };
                    } else {
                        return false;
                    }
                }
            }
        ],
        toDOM(mark) {
            let { value } = mark.attrs;
            return ["span", {
                style: `background-color: ${value};`
            }, 0]
        }
    }
}

// :: Schema
// This schema roughly corresponds to the document schema used by
// [CommonMark](http://commonmark.org/), minus the list elements,
// which are defined in the [`prosemirror-schema-list`](#schema-list)
// module.
//
// To reuse elements from this schema, extend or read from its
// `spec.nodes` and `spec.marks` [properties](#model.Schema.spec).

export function createSchema(packageResourceUrl: PackageResourceUrl) {

    // :: Object
    // [Specs](#model.NodeSpec) for the nodes defined in this schema.
    const nodes: Nodes = {
        // :: NodeSpec The top level document node.
        doc: {
            content: "block+"
        },

        // :: NodeSpec A plain paragraph textblock. Represented in the DOM
        // as a `<p>` element.
        paragraph: {
            content: "inline*",
            group: "block",
            parseDOM: [{ tag: "p" }],
            toDOM() { return pDOM }
        },

        // :: NodeSpec A blockquote (`<blockquote>`) wrapping one or more blocks.
        blockquote: {
            content: "block+",
            group: "block",
            defining: true,
            parseDOM: [{ tag: "blockquote" }],
            toDOM() { return blockquoteDOM }
        },

        // :: NodeSpec A horizontal rule (`<hr>`).
        horizontalRule: {
            group: "block",
            parseDOM: [{ tag: "hr" }],
            toDOM() { return hrDOM }
        },

        // :: NodeSpec A heading textblock, with a `level` attribute that
        // should hold the number 1 to 6. Parsed and serialized as `<h1>` to
        // `<h6>` elements.
        heading: {
            attrs: { level: { default: 1 } },
            content: "inline*",
            group: "block",
            defining: true,
            parseDOM: [{ tag: "h1", attrs: { level: 1 } },
            { tag: "h2", attrs: { level: 2 } },
            { tag: "h3", attrs: { level: 3 } },
            { tag: "h4", attrs: { level: 4 } },
            { tag: "h5", attrs: { level: 5 } },
            { tag: "h6", attrs: { level: 6 } }],
            toDOM(node) { return ["h" + node.attrs.level, 0] }
        },

        // :: NodeSpec The text node.
        text: {
            group: "inline"
        },

        // :: NodeSpec An inline image (`<img>`) node. Supports `src`,
        // `alt`, and `href` attributes. The latter two default to the empty
        // string.
        image: {
            inline: true,
            attrs: {
                imageType: { default: "Url" },
                src: { default: null },
                resourceKey: { default: null },
                file: { default: null },
            },
            group: "inline",
            draggable: true,
            parseDOM: [{
                tag: "img[src]", getAttrs(dom: HTMLElement) {
                    let imageType = dom.getAttribute("data-image-type");
                    let src = dom.getAttribute("src");
                    let resourceKey = dom.getAttribute("data-resource-key");
                    let file = (dom as any).file;
                    if (!imageType) {
                        //如果没有类型，就根据值的情况计算一个类型
                        if (resourceKey) {
                            imageType = ImageType.Resource;
                        } else if (file) {
                            imageType = ImageType.File;
                        } else {
                            imageType = ImageType.Url;
                        }
                    }
                    let attrs: any = {
                        imageType: imageType,
                    };
                    if (ImageType.Url == imageType) {
                        attrs.src = src;
                    } else if (ImageType.Resource == imageType) {
                        attrs.resourceKey = resourceKey;
                    } else if (ImageType.File == imageType) {
                        if (!file && src) {
                            file = BLOB_CACHE[src];
                        }
                        attrs.src = src;
                        attrs.file = file;
                    }
                    return attrs;
                }
            }],
            toDOM(node) {
                let { imageType, src, resourceKey, file } = node.attrs;
                let dom = document.createElement("img");
                dom.setAttribute("data-image-type", imageType);
                if (ImageType.Resource == imageType) {
                    src = packageResourceUrl(resourceKey);
                } else if (ImageType.File == imageType && !src && file) {
                    src = URL.createObjectURL(file);
                }
                dom.setAttribute("src", src);
                if (ImageType.File == imageType) {
                    (dom as any).file = file;
                } else if (ImageType.Resource == imageType) {
                    dom.setAttribute("data-resource-key", resourceKey);
                }
                return dom;
            }
        },

        // :: NodeSpec A hard line break, represented in the DOM as `<br>`.
        hardBreak: {
            inline: true,
            group: "inline",
            selectable: false,
            parseDOM: [{ tag: "br" }],
            toDOM() { return brDOM }
        }
    };
    return new Schema({ nodes, marks });
}